@font-face {
    font-family: 'Euclid Flex';
    src: url('fonts/EuclidFlex-LightItalic.woff2') format('woff2'),
        url('fonts/EuclidFlex-LightItalic.woff') format('woff');
    font-weight: 300;
    font-style: italic;
    font-display: swap;
}

@font-face {
    font-family: 'Euclid Flex';
    src: url('fonts/EuclidFlex-Semibold.woff2') format('woff2'),
        url('fonts/EuclidFlex-Semibold.woff') format('woff');
    font-weight: 600;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Monosten A';
    src: url('fonts/Monosten-A.woff2') format('woff2'),
        url('fonts/Monosten-A.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    --primary-color: #0059F4;
    --secondary-color: #6A6A6A;
}

body, html {
    width: 100%;
    height: 100%;
    overflow: hidden;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    user-select: none;
}

.wrapper {
    /* background-color: #F6F6F4; */
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    padding: 3.5vw;
}

.container {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
}

.col {
    position: relative;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.col {
    overflow: scroll;
}

.col.right {
    padding-left: 3.5vw;
}

#canvasContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    overflow: hidden;
}

#canvasContainer canvas {
  display: none;
}

#canvasContainer img {
    visibility: hidden;
    pointer-events: none;
    position: absolute;
}

.bio {
    font-family: 'Euclid Flex';
    font-weight: 600;
    color: var(--primary-color);
    font-size: 1.85vw;
    text-align: justify;
    margin-top: -10px;
}

.col.left {
    font-family: 'Monosten A';
    text-transform: uppercase;
    font-weight: 300;
    color: var(--primary-color);
    gap: 7px;
}

h2, h3, ul {
    font-size: 15px;
    line-height: 1;
}

.projects {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 7px;
}

a {
    text-decoration: none;
    color: var(--secondary-color);
}